@keyframes my-jump {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(10px);
  }
  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes my-scale {
  0% {
    transform: scale(1) ;
  }
  25% {
    transform: scale(0.95);
  }
  50% {
    transform: scale(1) ;
  }

  75% {
    transform: scale(0.95) ;
  }
  100% {
    transform: scale(1);
  }
}


 @keyframes playBtn {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    
  }
  25% {
    width: 50%;
    height: 50%;
    border-width: 2px;
    opacity: .5;
    
  }
  50% {
    width: 100%;
    height: 100%;
    border-width: 2px;
    opacity: 1;
  }

  75% {
    width: 50%;
    height: 50%;
    border-width: 2px;
    opacity: .5;
  }
  100% {
    width: 0;
    height: 0;
    border-width: 1px;
    opacity: 1;
  }
 }

@keyframes fadeinblock {
  0% {
    display: block;
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
// animation: fadeinblock ease .4s;

@keyframes myfadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
// animation: myfadein ease .4s;

@keyframes myfadeinup {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
// animation: myfadeinup ease .4s;

@keyframes myfadeout {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    display: none;
  }
}
// animation: myfadeout ease .4s;

@keyframes slideInDown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}
// animation: slideInDown .6s both;

@keyframes slideInUp {
  from {
    transform: translate3d(0, 110%, 0);
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
// animation: slideInUp .6s both;

@keyframes iconjump {
  0%,
  to {
    transform: translateY(0);
  }

  20%,
  70% {
    transform: translateY(3px);
  }

  50% {
    transform: translateY(-1px);
  }
}
// animation: iconjump 1s ease-in-out;

@keyframes iconscrollup {
  0% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(-110%);
  }
  65% {
    opacity: 0;
    transform: translateY(110%);
  }
  67% {
    opacity: 1;
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
// animation: iconscrollup .4s cubic-bezier(0.83, 0.23, 0.48, 0.99) forwards;

@keyframes shadowpulse {
  0% {
    box-shadow: 0 0 0 0 var(--color-primary);
