// switcher -----------------------------------------------------------/////
.switch {
  line-height: 0;

  .header__top & {
    display: none;

    @include media-breakpoint-up(lg) {
      display: block;
    }
  }

  &:not(:last-of-type) {
    margin-right: 15px;
  }

  &__label {
    position: relative;
    cursor: pointer;
  }

  &__input {
    opacity: 0;

    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
  }

  // variables
  // ----------------------------------------------------
  & {
    --width: 6rem;
    --height: 3rem;

    --thumb-size: 1.6rem;
    --thumb-background: #000;
    --checked-thumb-background: #fff;
    --thumb-border: 1px solid #fff;
    --checked-thumb-border: 1px solid #000;
    --thumb-space-between-edges: .6rem;
    --thumb-out: var(--thumb-space-between-edges);
  }

  &--3 {
    --width: 2.5rem;
    --height: 1.2rem;

    --thumb-size: 1.2rem;
    --thumb-space-between-edges: 0rem;
    --thumb-out: -.3rem;

    --border: 1px solid #fff;
    --background: transparent;
  }

  // appearance
  // ----------------------------------------------------
  &__design {
    display: inline-block;

    width: var(--width);
    height: var(--height);
    border: var(--border);
    background: var(--bg-primary);
    border-radius: 100rem;
    position: relative;

    transition: .2s, box-shadow 0s;
  }

  &__design::before {
    content: '';

    position: absolute;
    left: var(--thumb-out);
    top: 50%;
    transform: translateY(-50%);

    width: var(--thumb-size);
    height: var(--thumb-size);
    border-radius: 100rem;

    background: var(--bg-secondary);
    border: var(--thumb-border);
    transition: inherit;
  }


  // states
  // ----------------------------------------------------
   &__input:checked+&__design::before {
    left: calc(100% - (var(--thumb-size) + var(--thumb-out)));
    // background: var(--checked-thumb-background);
    // border: var(--checked-thumb-border);
  }

}
// end switcher --------------------------------------------------------/////

