// main-nav ----------------------------------------------------///////
.main-nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 280px;
  padding: 10px 5px 15px 20px;
  background: var(--color-primary);
  z-index: 2250;
  transition: all .4s;
  transform: translateX(120%);

  @include media-breakpoint-up(xl) {
    position: static;
    width: auto;
    margin-left: auto;
    margin-right: 20px;
    padding: 0;
    transform: none;
    // background: var(--bg-primary);
    transition: none;
    display: none;
  }

  &--open {
    transform: translateX(0);
  }

  .os-content {
    display: flex;
    flex-direction: column;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-right: 25px;

  @include media-breakpoint-up(xl) {
    flex-direction: row;
    height: initial;
  }
}

.main-nav__item {
  position: relative;
  margin-bottom: 8px;
  padding: 5px;
  padding-bottom: 8px;
  text-decoration: none;
  color: var(--black);
  font-size: 18px;
  line-height: 1.2em;
  transition: all .2s;
  font-weight: 700;
  border-bottom: 1px solid var(--gray);
  

  @include media-breakpoint-up(xl) {
    margin-bottom: 0;
    font-size: 16px;
  }

  &:hover {
    color: var(--color-primary);
  }
}

.main-nav__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.main-nav__close {
  position: relative;
  // top: 14px;
  // left: -38px;
  width: 35px;
  height: 35px;
  margin: 0;
  border-radius: 4px;
  cursor: pointer;
  background: #e6e6e6
}

.main-nav__close:hover {
  background: rgba(0,0,0,.7)
}

.main-nav__close-inner {
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

.main-nav__close-inner span {
  display: block;
  background: #000;
  border-radius: 2px
}

.main-nav__close-inner span:nth-child(1) {
  height: 80%;
  width: 4px;
  position: absolute;
  top: 10%;
  left: 43%;
  -webkit-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s
}

.main-nav__close-inner span:nth-child(2) {
  width: 80%;
  height: 4px;
  position: absolute;
  left: 10%;
  top: 44%;
  -webkit-transition-delay: .25s;
  -o-transition-delay: .25s;
  transition-delay: .25s
}

.nav-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.7);
  z-index: 2240;

  &--open {
    display: block;
    animation: myfadein ease .4s;

    @include media-breakpoint-up(xl) {
      display: none;
    }
  }
}
// main-nav end ------------------------------------------------///////

